@charset "utf-8";

@import "pretendard/dist/web/static/pretendard.css";

*{margin: 0; padding: 0;}
a{color: #333; text-decoration: none;}
ul, li, ol{list-style: none;}
body{font-family: "Pretendard", sans-serif, "Noto Sans KR"; color: #333;}
main{padding-top: 140px;}
:root {
  /* color */
--color-primary: #18a0fb;
--color-blue-900: #1e3a8a;
--color-blue-800: #1e40af;
--color-blue-700: #1d4ed8;
--color-blue-600: #2563eb;
--color-blue-500: #3b82f6;
--color-blue-400: #60a5fa;
--color-blue-300: #93c5fd;
--color-blue-200: #bfdbfe;
--color-blue-100: #dbeafe;
--color-blue-50:  #eff6ff;
--color-secondary: #64748b;


     /* Label (Text 계열) */

--color-label-900: #111827;
--color-label-800: #1f2937;
--color-label-700: #374151;
--color-label-500: #6b7280;
--color-label-100: #d1d5db;


     /* Background */
--color-background-default: #ffffff;
--color-background-alternative: #f9fafb;

/*Line*/
--color-line-800: #1f2937;
--color-line-400: #9ca3af;
--color-line-200: #e5e7eb;

/*Status (상태)*/
  --color-status-correct: #22c55e;  /* green-500 */
  --color-status-error:   #ef4444;  /* red-500 */
  --color-status-caution: #f59e0b;  /* amber-500 */
  --color-status-disable: #9ca3af;  /* gray-400 */

/*Component*/
--color-component-dark: #111827;
--color-component-light: #f3f4f6;
--color-component-assistive: #6b7280;
--color-component-alternative: #e5e7eb;
--color-component-material: #ffffff;

/*Effects / Etc*/
--color-dimmed: rgba(0, 0, 0, 0.5);
--color-scroll: #9ca3af;

--elevation-shadow-normal: 0 1px 2px rgba(0,0,0,0.1);
--elevation-shadow-strong: 0 4px 6px rgba(0,0,0,0.15);
--elevation-shadow-heavy:  0 10px 15px rgba(0,0,0,0.25);
}


header{display: flex; position: fixed; width: 100%; height: 60px; border-bottom: 1px solid #dfe3e8; z-index: 10; background: #fff;}
/* 로고서식 */
.header-container{width: 1440px; margin:0 auto}
.header-container h1{transform: translateY(10px);}

/* 회원가입, 로그인 버튼 */
.auth-form{float: right; transform: translateY(-25px);}
.auth-form a{display: inline-block; border: 1px solid #dfe3e8; text-align: center; padding: 8px 16px; border-radius: 6px; margin-right: 10px; font-size: 0.875rem;}
.auth-form a:first-child{color:var(--color-primary); border: 1px solid var(--color-blue-300)}

.auth-form .logout-btn{display: inline-block; border: 1px solid #dfe3e8; text-align: center; padding: 8px 16px; border-radius: 6px; margin-left: 10px; font-size: 0.875rem; background: none; cursor: pointer; transition: 0.3s;}

.auth-form a:hover, .auth-form .logout-btn:hover{background: #f5f5f5;}


/* 푸터서식 */
footer{width: 100%; height: 300px; border-top: 1px solid #dfe3e8; padding-top: 60px;}
footer .footer-inner{width: 1440px; margin: 0 auto; display: flex; justify-content: space-between;}

/* 좌측 */
footer .grow{width: 70%; }
footer .grow .footer-nav{padding: 20px 0px; font-size: 0.875rem;}
footer .grow .footer-nav a{font-size: 0.875rem;}
footer .grow .footer-nav a:nth-child(3){font-weight: bold;}
footer .grow .footer-nav a:hover{font-weight: bold;}

footer .grow .footer-info1{font-size: 0.938rem; line-height: 25px; color: #b8b8b8;}
footer .grow .footer-info2{font-size: 0.938rem; color: #b8b8b8;}
footer address{font-style: normal; padding:20px 0px; color: #b8b8b8; font-size: 0.938rem;}
footer ul{display: flex;}


/* 우측 */
footer .cs-center{width: 20%;}
footer .cs-center h3{color: #62747e; font-size: 0.938rem; padding-bottom: 10px;}
footer .cs-center a{color: var(--color-primary); font-size: 3rem; font-weight: bold;}
footer .cs-center h4{color: var(--color-line-400); font-size: 0.938rem; padding: 10px 0px 6px 0px; font-weight: 400;}
footer .cs-center p{font-size: 0.875rem; line-height: 20px; color:#6e747e;}


/* 태블릿 (768px ~ 1024px) */
@media screen and (max-width: 1024px) and (min-width: 768px) {
  header {
    width: 100%;
  }
  .header-container {
    width: 100% !important;
    padding: 0 24px;
    margin: 0 auto;
  }
  footer {
    width: 100% !important;
    padding: 40px 24px 0 24px;
    box-sizing: border-box;
  }
  .footer-inner {
    width: 100% !important;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    gap: 24px;
    justify-content: none;
  }
  .grow {
    width: 70%;
    margin-top: 0;
  }
  .cs-center {
    width: 30%;
    margin-bottom: 0;
  }
  .cs-center a{font-size: 1.5rem !important;}
  .footer-inner .grow ul {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    padding: 0;
    margin: 0;
    justify-content: flex-start;
  }
  .footer-inner .grow ul li {
    flex: 0 0 40%;
    max-width: 120px;
    margin-bottom: 8px;
    box-sizing: border-box;
  }
  .footer-inner .grow ul img {
    max-width: 100%;
    height: auto;
    display: block;
  }
}

/* 모바일 */
@media screen and (max-width: 766px) {
  header{width: 100% !important;}
  .header-container{width: 100% !important; padding: 0px 20px;}

  footer { 
    width: 100% !important;position: relative;
  }
  .footer-inner {
    width: 90% !important;
    display: flex;
    flex-direction: column-reverse;
    gap: 24px;
    margin: 0 auto;
  }
  .grow {
    width: 100%;
    margin-top: 0;
    
  }
  .cs-center {
    width: 100%;
    margin-bottom: 0;
  }
  .cs-center p, .cs-center a {
    white-space: nowrap;
  }
    
  /* 푸터 네비 */
  .footer-nav {
    display: flex;
    flex-wrap: wrap;
    gap: 3px; /* a 태그 사이 간격 */
  }
  .footer-nav a {
    display: inline-block;
    margin-bottom: 8px;
    font-size: 0.875rem;
  }


  /* 푸터 마크이미지 */
  .footer-inner .grow ul {
    display: flex;
    flex-wrap: wrap;
    padding: 0;
    margin: 0;
    justify-content: flex-start;
  }
  .footer-inner .grow ul li {
    flex: 0 0 auto;
    width: auto;
    margin-bottom: 8px;
  }
  .footer-inner .grow ul img {
    max-width:100px;
    height: auto;
    display: block;
  }
}