@charset "utf-8";


/* 블로그 서식 */
.blog{width: 1440px; margin:0 auto; height: 500px; }
.blog div{display: flex; justify-content: space-between; line-height: 130%;}
.blog div h2{font-size: 2rem; color: #32373f;}

/* 검색창 */
.search{position: relative;}
.search input{padding: 15px 16px 15px 48px; width: 468px; border-radius: 5px; border:1px solid #e9e9e9;}
.search label{position: absolute; top: 15px; left: 20px; color: #a0a0a0;}

/* 블로그 이미지 */
.blog_img{width: 1440px; padding-top: 50px;
gap: 24px;}
.blog_img div{overflow: hidden; border-radius: 10px;}
.blog_img img{width: 700px; height: 368px; border-radius: 10px; transition: 0.3s;}
.blog_img img:hover{transform: scale(1.05); }

/* 태블릿 (768px ~ 1024px) */
@media screen and (max-width: 1024px) and (min-width: 767px) {
  main{width: 98%; margin: 0 auto;}
  .blog{width: 100%;}

 /* 블로그 이미지 */
  .blog_img{width: 100%; display: flex; justify-content: space-between;}
  .blog_img div{width: 100%;}
  .blog_img div img{width: 100%; height: 250px !important; margin-right: 10px;
}
}
@media screen and (max-width: 766px) {
  .blog{width: 100%;}
  .blog > div{display: block; width: 90%; margin: 0 auto;}
  .blog > div h2{padding-bottom: 50px;}
  .blog > div input{width: 100%;}

  /* 블로그 이미지 */
  .blog_img{width: 100%;}
  .blog_img div{width: 100%;}
  .blog_img div img{width: 100%; height: 200px;}
}