@charset "utf-8";

/* 에러메세지 서식 */
.error-input {border: 1px solid red;}
.error-msg{color: red; font-size: 0.875rem; margin-top:4px;}

/* 약관동의 서식 */
input{border: 1px solid #dfe3e8;}
.join{padding-top: 140px; padding-bottom: 2.5rem; width: 480px; margin: 0 auto;}
.join form .title{text-align: center;}
.join form .title h1{ font-size:2.5rem; font-weight: 400;}
.join form .title span{ font-size: 2.5rem; font-weight: bold;}

.join .bar-info{width: 100%; display: flex; justify-content: space-between; margin-top: 30px; font-size: 0.875rem; color: #18a0fb;}
.join .bar-info span:last-child{font-weight: bold;}
.join .bar{width: 100%; height: 10px; background: #f1f5f9; margin: 10px 0px; border-radius: 14px;}

.join .all-check{border-bottom: 1px solid #dfe3e8; font-size: 1.125rem;}
.join .all-check label{font-weight: 500; color: #333;}

.join .join-form p{line-height: 3rem;}
.join .join-form p a{float: right; color: #6e747e;}
.join input[type='checkbox']{width: 20px; height: 20px;  vertical-align: middle; margin-right: 10px;}
.join label{vertical-align: middle; color: #6e747e; cursor: pointer;}
.join .next-btn{width: 100%; height: 56px; margin-top: 30px; background: #cbd5e1; color: #fff; border: none; font-size: 1rem; font-weight: bold;   cursor: not-allowed; border-radius: 12px;}

/* 활성화 */
.next-btn.active {
  background: #18a0fb;
  color: #fff;
  cursor: pointer;
}


/*약관동의 후 회원가입 서식*/
.join .register-form .bar .bar-inner{width: 15%; height: 10px; background: #18a0fb; margin: 10px 0px; border-radius: 14px; transition: 0.3s;}

.join .register-form .id-input, .join .register-form .password-input, .join .register-form .detail-info{
  margin-top: 20px;
}
.join .register-form p .verify-btn{cursor: pointer; width: 100px; height: 42px; font-size: 0.875rem; margin-left: 10px; border-radius: 8px; border: 1px solid #dfe3e8; background: none; color: #18a0fb; transition: 0.3s;} 
.join .register-form button:hover{background: #f5f5f5;}
.join .register-form .find-number{font-size: 0.875rem; color: #6e747e; float: right; padding-bottom: 1px; border-bottom: 1px solid #6e747e;}
.join .register-form label{font-size: 0.875rem; font-weight: bold; margin-top: 30px;}
.join .register-form .password-label{margin-top: 20px;}
.join .register-form input[type='text']{width: 70%; padding: 12.5px 16px; border-radius: 8px; margin-top: 10px;}

.join .register-form .password-input input{width: 431px; padding: 12.5px 35px 16px 12.5px; border-radius: 8px; margin-top: 10px;}

/*인증 후 나오는 detail-info 서식*/
.join .register-form .detail-info div{margin-top: 20px;}
.join .register-form .detail-info input{width: 446px;}
.join .register-form .detail-info input[type='date']{
padding: 12.5px 16px; border-radius: 8px; margin-top: 10px;
}
.join .register-form .detail-info input[type='email']{
padding: 12.5px 16px; border-radius: 8px; margin-top: 10px;
}
.join .register-form .detail-info .join-btn{width: 100%; height: 56px; margin-top: 30px; background: #cbd5e1; color: #fff; border: none; font-size: 1rem; font-weight: bold;   cursor: not-allowed; border-radius: 12px;} 
.join .register-form .detail-info .join-btn.active{background: #18a0fb; color: #fff; cursor: pointer; width: 100%; height: 56px; margin-top: 30px;  border: none; font-size: 1rem; font-weight: bold; border-radius: 12px;}

/* 로그인 서식 */
.login{padding-top: 60px;}
.login-header{width: 100%; background: #f9fafb; height: 40px;}
.login-header-inner{width: 1440px; font-size: 0.875rem; color: #6e747e; margin: 0 auto; text-align: right;}
.login-header-inner p{transform: translateY(10px);}

.login-form{width: 440px; margin: 0 auto; padding: 80px 20px 40px;}
.login-form h2{font-weight: 500; font-size: 26px;}
.login-form label{font-size: 0.875rem; color: #6e747e;}
.login-form input[type='text'],
.login-form input[type='password']
{width: 400px; padding: 12.5px 16px; border-radius: 8px; border:1px solid #dfe3e8;}
.login-form div{margin-top: 2rem;}

/* 체크박스 */
.login-form .idsave {margin-top: 2rem;}
.login-form .idsave input[type='checkbox']{width: 20px; height: 20px;}
.login-form .idsave label{font-size: 0.875rem; color: #6e747e; transform: translateY(-4px); display: inline-block; padding-left: 5px;}
/* 로그인, 회원가입 버튼 */
.login-join-btn{width: 100%;}
.login-join-btn .login-btn{width: 440px; padding: 12.5px 5px; font-size: 1rem; background: #18a0fb; border: none; color: #fff; border-radius: 12px; cursor: pointer; font-weight: bold;}
.login-join-btn a{width: 425px; display: inline-block; text-align: center; border: 1px solid #18a0fb; margin-top: 1rem; padding: 12.5px 6px; border-radius: 12px; font-weight: bold; color: #18a0fb; font-weight: bold;}

/* 테블릿 */
@media screen and (max-width: 1024px) and (min-width: 768px) {
  .login-header-inner{width: 100%;}
  .login-header-inner p{padding-right: 10px;}
}

/* 모바일 */
@media screen and (max-width: 767px) {
  .join{width: 90%;}
  .join .join-form .title h1,
  .join .join-form .title span,
  .join .register-form .title h1,
  .join .register-form .title span
  {font-size: 1.6rem;}
  .join .register-form{width: 90%; margin: 0 auto;}

  .detail-info input,.join .register-form .password-input input {
    width: 100% !important;
    max-width: 100%;
    box-sizing: border-box;
  }
  .join .register-form .id-input input[type='text']{width: 52% !important;}

  /* 로그인 폼 */
  .login{width: 100%;}
  .login-header-inner{width: 100%;}
  .login-header-inner p{padding-right: 10px;}
  .login-form{width: 90%;}
  .login-form input[type='text'],
  .login-form input[type='password']{
    width: 90% !important;
  }
  .login-join-btn .login-btn{width: 100%;}
  .login-join-btn .join-btn{width: 97%;}
}