@charset "utf-8";

/* 탭메뉴 서식 */

.tabmenu-section {width: 100%;}
/* 탭 메뉴 */
.tabmenu-tabs {
  display: flex;          
  gap: 1.5rem;             
  border-bottom: 1px solid #e5e7eb; 
  padding-bottom: 0.5rem;  
  position: relative; 
  width: 1440px;
  margin: 0 auto;
}

.tabmenu-tab {
  position: relative;     
  cursor: pointer;        
}
.tabmenu-tab span {
  padding-bottom: 0.5rem; 
}

.tabmenu-tab.selected span {
  font-weight: 700;        
  color: #000000;        
}

.tabmenu-tab span:not(.selected) {
  color: #6b7280;         
}

/* 탭 하단 언더라인 */
.tabmenu-underline {
  position: absolute; 
  bottom: -10px;
  left: 0;
  width: 100%;
  height: 2px;             
  background-color: #000;  
  border-radius: 9999px;   
}

/* 카드 그리드 */
.tabmenu-grid {
  display: grid;
  width: 1440px;
  margin: 1.5rem auto;
  grid-template-columns: repeat(4, 1fr); 
  gap: 1rem;            
}

/* 카드 개별 */
.tabmenu-card {
  border-radius: 0.25rem;      
  overflow: hidden;             
  border: 1px solid #e5e7eb;   
  cursor: pointer;              
  transition: box-shadow 0.3s;  
}
.tabmenu-card:hover {box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1),0 4px 6px -2px rgba(0, 0, 0, 0.05); 
}

/* 카드 이미지 */
.tabmenu-card img {
  width: 100%;        
  height: 9rem;       
  object-fit: cover;   
}

/* 카드 내용 */
.tabmenu-card-content {
  padding: 0.5rem;  
}

.tabmenu-card-title {
  font-size: 0.875rem; 
  font-weight: 600;    
  margin-bottom: 0.25rem; 
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

.tabmenu-card-date {
  font-size: 0.75rem; 
  color: #6b7280;      
  margin: 0;
  padding: 0;
  line-height: 1.2;
}

.tabmenu-message {
  grid-column: span 4;     
  text-align: center;       
  color: #000;           
}

/* 페이지네이션  */
.tabmenu-pagination {
  display: flex;
  justify-content: center;
  gap: 8px;
  margin: 20px 0px;
}

.tabmenu-pagination button {
  padding: 8px 12px;
  border: none;
  background: none;
  cursor: pointer;
}

.tabmenu-pagination button.active {
  background: #f1f5f9;
  color: #333;
  border-color: #f1f5f9;
  font-weight: bold;
  border-radius: 50%;
}

.tabmenu-pagination button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}


/* 태블릿 (768px ~ 1024px) */
@media screen and (max-width: 1024px) and (min-width: 768px) {
  .tabmenu-tabs{width: 90%;}
  .tabmenu-grid{
    grid-template-columns: repeat(2, 1fr); width: 90%;
    margin: 10px auto;
  }
}

/* 모바일 */
@media (max-width: 767px) {
  .tabmenu-section{margin-top: 100px;}
  .tabmenu-tabs{width: 90%;}
  .tabmenu-tab{width:100%; margin: 0 auto;} 
  .tabmenu-tabs span{font-size: 0.8rem; white-space: nowrap;}
  .tabmenu-underline {
    width: 100%;
    left: 0;
    bottom: -10px;
  }
  .tabmenu-grid{
    grid-template-columns: repeat(1, 1fr); width: 90%;
    margin: 30px auto;
  }
}